export default function UserBlock() {
  const { allMessages, setAllMessages , userPrompt} = useContext(userContext)!;

  function xmlToString(xml: string) {
    const parserForXml = new XMLParser({
      ignoreAttributes: false,
      attributeNamePrefix: "",
      textNodeName: "content",
      trimValues: true,
    });


    const parsedXML = parserForXml.parse(xml);
    let parsedBoltActions: BoltAction[];

    if (Array.isArray(parsedXML.boltArtifact.boltAction)) {
      parsedBoltActions = parsedXML.boltArtifact.boltAction;
    } else {
      parsedBoltActions = [parsedXML.boltArtifact.boltAction];
    }

    let allSteps: Step[] = [];

    for (let i = 0; i < parsedBoltActions.length; i++) {
      let newStep: Step = {
        title: parsedBoltActions[i].filePath,
        status: "Pending",
        content: parsedBoltActions[i].content,
      };

      allSteps.push(newStep);
    }

    return allSteps;
  }


  async function handlePrompt(userPrompt : string){
        try {
            const response = await axiosInstance.post("/template" , {
                userPrompt
            })
            
            console.log(response.data);
            return response.data
        }
        catch (err) {
            console.log(err);
            return { BASE_PROMPT : "" , uiPrompt : "" , userPrompt : ""}
        }
    }


  type ResponseForTemplate = { BASE_PROMPT : string , uiPrompt : string , userPrompt : string};


  async function promptResponseFromAi(postBody : ResponseForTemplate) {
    try {
      const response = await axiosInstance.post("/take-prompt", postBody);
      const correctedXML = xmlToString(response.data);
      console.log(correctedXML);
      let dummyMessages = [...allMessages!];
      dummyMessages[dummyMessages?.length - 1].ai = correctedXML!;
      // allMessages = dummyMessages;
      console.log(dummyMessages);
      setAllMessages(dummyMessages);
    } catch (err) {
      toast.error("Ai Model is not Running");
    }
  }

  useEffect(() => {
    async function callAtLanding() {
      const response  : ResponseForTemplate = (await handlePrompt(userPrompt));
      promptResponseFromAi(response);
    }
    
    //calling at landing..
    callAtLanding();
  }, []);
  

  return (
    <div>
      <Card>
        <CardContent>
          {allMessages
            ? allMessages.map((item, index) => (
                <div className="flex flex-col gap-1" key={index}>
                  <div className="flex justify-end">
                    <span>{item.user}</span>
                  </div>

                  <div className="flex justify-start">
                    {item.ai.map((newItem, index) => (
                      <Card key={index}>
                        <CardContent className="flex gap-3">
                          {newItem.status == "Pending" ? (
                            <TicketCheckIcon />
                          ) : (
                            <ListChecksIcon />
                          )}
                          {newItem.title}
                        </CardContent>
                      </Card>
                    ))}
                  </div>
                </div>
              ))
            : ""}
        </CardContent>
      </Card>
    </div>
  );
}
